<!--suppress HtmlUnknownTag, HtmlUnknownAttribute, CheckEmptyScriptTag -->
<template>
    <v-container >
        <h2>Contact form</h2>

        <v-expansion-panels>
            <v-expansion-panel hover focusable inset >
                <v-expansion-panel-header>
                    <p>
                        If you wish to contact me and I've bailed out of any and all social media, or you have, or we just don't have an overlap
                        in such more “conventional” ways of communication, you may use the form below. Do include a contact form of your own if I
                        am to get back to you!
                    </p>
                </v-expansion-panel-header>
                <v-expansion-panel-content>
                    <p>
                        This form may not work at times due to stringent anti-spam measures; if it's disabled or errors out, refresh the page later.
                        And when it does, I may not immediately receive your message; it'll be in a database somewhere.
                    </p>
                    <p>
                        You may find me on things like (in decreasing order of preference):
                    </p>
                    <ul>
                        <li>Discord</li>
                        <li>Signal</li>
                        <li>generic email</li>
                        <li>LinkedIn</li>
                        <li>WhatsApp</li>
                        <li>(literally anything else)</li>
                        <li>Facebook</li>
                    </ul>
                </v-expansion-panel-content>
            </v-expansion-panel>
        </v-expansion-panels>
        <p></p>

        <span>
            <v-text-field class="ctrl-enter" filled outlined v-model="title" label="Title" :disabled="!formElementsEnabled" />

            <v-textarea class="ctrl-enter" filled outlined v-model="content" label="Message" :disabled="!formElementsEnabled" />

            <v-file-input v-if="uploadVisible || uploadVisibleOverride"
                          small-chips v-model="file" label="Upload file" :disabled="!formElementsEnabled" />

            <v-btn :disabled="!formElementsEnabled" :loading="restCallsGoing > 0"
                   width="inherit" elevation="7" x-large
                   @click="submitMessage()">
                Send&emsp;<small style="opacity:0.7">(Ctrl+Enter)</small>
            </v-btn>
        </span>

        <Notifications :alerts="alerts" :floating="true" />
    </v-container>
</template>

<script>
  import AxiosHelper from '../components/AxiosHelper.vue';
  import Notifications from '../components/Notifications.vue';

  export default {
    components: { Notifications },

    mixins: [AxiosHelper],

    props: ['uploadVisible'],

    data: () => ({
      formEnabled: false,
      uploadVisibleOverride: false,

      title: '',
      content: '',
      file: null
    }),

    computed: {
      formElementsEnabled () {
        return this.formEnabled && !this.restCallsGoing;
      }
    },

    watch: {
      content () {
        if (this.content === 'upload')
          this.uploadVisibleOverride = true;
      }
    },

    methods: {
      submitMessage () {
        let formData = new FormData();
        formData.append('title', this.title);
        formData.append('content', this.content);
        formData.append('receivedFrom', location.host);
        if (this.file)
          formData.append('file', this.file, this.file.name);

        this.credentialedAxios('submitting form', axios => axios.post(this.$apiUrl + '/contact', formData), this.onSubmitSuccessful)
          .then(() => this.checkEnabled());
      },

      onSubmitSuccessful() {
        this.alerts.push({type: 'info', text: 'Got it!'});
        this.title = '';
        this.content = '';
        this.file = null;
      },

      checkEnabled() {
        this.credentialedAxios('checking whether the form is enabled', axios => axios.get(this.$apiUrl + '/contact/enabled'),
          data => this.formEnabled = data);
      }
    },

    mounted () {
      this.checkEnabled();

      document.querySelectorAll('.ctrl-enter input, .ctrl-enter textarea')
        .forEach(fld => fld.onkeydown = key => {
          if (key.keyCode === 13 && key.ctrlKey)
            this.submitMessage()
      });
    }
  }
</script>

